:root {
  --active-link: #00acdc;
  --text-color: #000;
  --bg-color: #fff;
  --shadow-color: 0px 2px 4px 0px #0000000d;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  background-color: var(--bg-color);
  box-shadow: 0 2px 4px 0 var(--shadow-color);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav {
  align-items: center;
  height: 60px;
  margin-right: 50px;
  display: flex;
}

.logo-link {
  align-items: center;
  margin-left: 50px;
  display: flex;
}

.lists {
  height: 100%;
  padding: 0;
  list-style: none;
  display: flex;
}

.list {
  height: 100%;
  margin: 0 10px;
  position: relative;
}

.link {
  color: var(--text-color);
  text-transform: uppercase;
  align-items: center;
  height: 100%;
  font-size: 12px;
  text-decoration: none;
  display: flex;
}

.first-list {
  margin-left: 0;
}

.last-list {
  margin-right: 0;
}

a.is-active {
  color: var(--active-link);
}

.first-list:after, .is-active:hover:after {
  content: "";
  background-color: var(--active-link);
  border-radius: 20px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.is-active:hover .link {
  color: var(--active-link);
}

.center-list:hover:after {
  display: none;
}
